.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
  background: #1a1a1d;
}

.messages::-webkit-scrollbar {
  width: 12px;
}

.messages::-webkit-scrollbar-track {
  background: #0d0e11e5;
}

.messages::-webkit-scrollbar-thumb {
  background-color: rgb(76, 76, 97);
  border-radius: 20px;
}

.messages button::before {
  content: "👀";
}
